import React, { useCallback, useEffect, useRef, useState } from 'react';
import { SubmitHandler, useForm } from 'react-hook-form';
import { useParams } from 'react-router-dom';
import { useReactToPrint } from 'react-to-print';
import { QuoteDetailService } from '../../services/quoteDetailService';
import QuoteDetailPrint from '../../components/QuoteDetailPrint';
import {
  Button,
  ButtonIcon,
  Column,
  InputForm,
  LabelCustom,
  Panel,
  Row,
  SelectForm,
  Text
} from '../../components';
import QuotesItems from '../../components/QuotesItems';
import { useMainContext } from '../../Context/Main';
import { useContextQuoteItems } from '../../Context/QuoteItems';
import {
  IParamQuoteId,
  IPaymentConditionsSelect,
  IQuoteCSV,
  IQuoteItem,
  IShippingTypeSelect,
  IShippingAndPaymentForm
} from '../../Interfaces/QuotesDetail';
import { IBuyer } from '../../Interfaces/Buyer';
import { ISupplier } from '../../Interfaces/Supplier';

import {
  mocaBuyer,
  mocaSelectPaymentConditions,
  mocaSelectShipping,
  mocaShippingAndPayment,
  mocaSupplier
} from '../../MoocasData/QuotesDetail';
import { QuoteItemsService } from '../../services/quoteItemsService';
import { FileCsv, Save, Print } from '../../styles/Icons';
import { buildURI } from '../../utils/exportCSV';
import {
  mapQuoteCSVToQuoteItem,
  mapQuoteItemToQuoteCSV
} from '../../utils/mappersCSV';
import {
  Container,
  IconHand,
  Import,
  WrapperTitleQuotes,
  WrapperTop
} from './styles';

const QuotesDetail: React.FC = () => {
  const { quoteId } = useParams<IParamQuoteId>();

  const { quoteItemsContext, setQuoteItemsContext } = useContextQuoteItems();

  const [selectShipping, setSelectShipping] = useState<IShippingTypeSelect[]>(
    []
  );

  const [selectPaymentConditions, setSelectPaymentConditions] = useState<
    IPaymentConditionsSelect[]
  >([]);

  const [buyer, setBuyer] = useState<IBuyer>({} as IBuyer);

  const [supplier, setSupplier] = useState<ISupplier>({} as ISupplier);

  const inputUpload = useRef<HTMLInputElement>(null);

  const linkExportCSV = useRef<HTMLAnchorElement>(null);

  const quoteDetailPrint = useRef<HTMLDivElement>(null);

  const {
    register,
    handleSubmit,
    reset,
    getValues,
    formState: { errors }
  } = useForm<IShippingAndPaymentForm>();

  const { setLoading } = useMainContext();

  const onSubmit: SubmitHandler<IShippingAndPaymentForm> = useCallback(
    async (item: IShippingAndPaymentForm) => {
      try {
        setBuyer(prevState => ({ ...prevState, ...item }));

        setLoading(true);
        await QuoteDetailService.UpdateShippingAndPayment(item);
        reset(item);
        setLoading(false);
      } catch (err) {
        console.log(err);
      }
    },
    [reset, setLoading]
  );

  const handlerPrint = useReactToPrint({
    content: () => quoteDetailPrint.current
  });

  const handlerExportCSV = useCallback(() => {
    const newData = mapQuoteItemToQuoteCSV(quoteItemsContext);
    const URI = buildURI(newData);

    linkExportCSV?.current?.setAttribute('download', 'download.csv');
    linkExportCSV?.current?.setAttribute('hidden', '');
    linkExportCSV?.current?.setAttribute('href', URI);

    linkExportCSV?.current?.click();
  }, [quoteItemsContext]);

  const handlerImportCSV = useCallback(() => {
    const csv = '';
    if (inputUpload.current) inputUpload.current.value = '';
  }, []);

  useEffect(() => {
    // pegar valor de endpoint
    setBuyer(mocaBuyer);

    // pegar valor de endpoint
    setSupplier(mocaSupplier);

    // pegar valor de endpoint
    setSelectShipping(mocaSelectShipping);

    // pegar valor de endpoint
    setSelectPaymentConditions(mocaSelectPaymentConditions);

    const getQuoteItems = async () => {
      try {
        setLoading(true);
        const items = await QuoteDetailService.GetQuoteItems(quoteId);
        setLoading(false);
        setQuoteItemsContext(items);
      } catch (err) {
        console.log(err);
      }
    };

    const getShippingAndPayment = async () => {
      try {
        setLoading(true);
        const item = await QuoteDetailService.GetShippingAndPaymentForm(
          quoteId
        );
        reset(item);
        setLoading(false);
      } catch (err) {
        console.log(err);
      }
    };

    getQuoteItems();
    getShippingAndPayment();
  }, [quoteId, reset, setQuoteItemsContext, setLoading]);

  return (
    <>
      <QuoteDetailPrint
        ref={quoteDetailPrint}
        quote={quoteItemsContext}
        supplier={supplier}
        buyer={buyer}
        shipping={selectShipping}
        payment={selectPaymentConditions}
        paymentAndShipping={getValues()}
      />
      <Container>
        <WrapperTop>
          <ButtonIcon
            text="Exportar"
            colorIcon="green-csv"
            onClick={() => handlerExportCSV()}
          >
            <FileCsv />
            <a
              target="_blank"
              ref={linkExportCSV}
              href="empty"
              style={{ display: 'none' }}
              rel="noreferrer"
            >
              link
            </a>
          </ButtonIcon>
          <Import>
            <input
              accept=".csv"
              ref={inputUpload}
              id="file-upload"
              type="file"
              required
            />
            <label htmlFor="file-upload">
              <ButtonIcon text="Importar" colorIcon="green-csv">
                <FileCsv />
              </ButtonIcon>
            </label>
            <ButtonIcon
              className="import"
              text="Salvar"
              colorIcon="white"
              backColor="primary"
              textColor="white"
              onClick={() => handlerImportCSV()}
            >
              <Save />
            </ButtonIcon>
          </Import>

          <ButtonIcon
            text="Imprimir"
            colorIcon="black-print"
            onClick={handlerPrint}
          >
            <Print />
          </ButtonIcon>
        </WrapperTop>
        <Panel>
          <Row>
            <Column>
              <WrapperTitleQuotes>
                <IconHand />
                <span>{quoteId}</span>
              </WrapperTitleQuotes>
            </Column>
          </Row>
          <Row>
            <Column>
              <LabelCustom>Empresa</LabelCustom>
              <Text>{buyer?.companyName}</Text>
            </Column>
          </Row>
          <Row>
            <Column>
              <LabelCustom>Data Emissão</LabelCustom>
              <Text>{buyer?.issuanceDate}</Text>
            </Column>
            <Column>
              <LabelCustom>Data Limite</LabelCustom>
              <Text>{buyer?.deadlineDate}</Text>
            </Column>
          </Row>
          <Row>
            <Column>
              <LabelCustom>CEP</LabelCustom>
              <Text>{buyer?.zipCode}</Text>
            </Column>
          </Row>
          <Row>
            <Column>
              <LabelCustom>Endereço</LabelCustom>
              <Text>{buyer?.address}</Text>
            </Column>
            <Column>
              <LabelCustom>Numero</LabelCustom>
              <Text>{buyer?.number}</Text>
            </Column>
          </Row>
          <Row>
            <Column>
              <LabelCustom>Complemento</LabelCustom>
              <Text>{buyer?.complement}</Text>
            </Column>
            <Column>
              <LabelCustom>Bairro</LabelCustom>
              <Text>{buyer?.neighborhood}</Text>
            </Column>
          </Row>
          <Row>
            <Column>
              <LabelCustom>UF</LabelCustom>
              <Text>{buyer?.state}</Text>
            </Column>
            <Column>
              <LabelCustom>Cidade</LabelCustom>
              <Text>{buyer?.city}</Text>
            </Column>
          </Row>
          <form
            style={{ width: '100%' }}
            onSubmit={handleSubmit(onSubmit)}
            noValidate
          >
            <Row>
              <Column>
                <SelectForm
                  register={register('shippingTypeId', {
                    required: 'Campo Obrigatório'
                  })}
                  label="Tipo Frete"
                >
                  <option key="-1" value="-1">
                    Selecione
                  </option>
                  {selectShipping.map(
                    ({ shippingTypeId: id, shippingType: type }) => (
                      <option value={id} key={id}>
                        {type}
                      </option>
                    )
                  )}
                </SelectForm>
              </Column>
            </Row>
            <Row>
              <Column>
                <SelectForm
                  register={register('paymentConditionsId', {
                    required: 'Campo Obrigatório'
                  })}
                  label="Cond. Pagamento"
                >
                  <option value="-1" key="-1">
                    Selecione
                  </option>
                  {selectPaymentConditions.map(
                    ({
                      paymentConditionsId: id,
                      paymentCondition: condition
                    }) => (
                      <option value={id} key={id}>
                        {condition}
                      </option>
                    )
                  )}
                </SelectForm>
              </Column>
            </Row>
            <Row>
              <Column>
                <InputForm
                  label="Valor Frete"
                  register={register('shippingValue', {
                    required: 'Campo Obrigatório'
                  })}
                  placeholder="Digite o Valor"
                  type="number"
                />
              </Column>
            </Row>
            <Row>
              <Column>
                <Button type="submit" outlined>
                  Salvar
                </Button>
              </Column>
            </Row>
          </form>
        </Panel>

        <QuotesItems />
      </Container>
    </>
  );
};
export default QuotesDetail;
