import {
  IQuoteItem,
  IShippingAndPaymentForm
} from '../Interfaces/QuotesDetail';
import {
  mocaShippingAndPayment,
  mocaQuotesItems
} from '../MoocasData/QuotesDetail';

export const QuoteDetailService = {
  UpdateShippingAndPayment: (item: IShippingAndPaymentForm) =>
    new Promise<string>(resolve => {
      setTimeout(() => {
        // update na base
        resolve('done!');
      }, 2000);
    }),

  GetShippingAndPaymentForm: (quoteId: String) =>
    new Promise<IShippingAndPaymentForm>(resolve =>
      setTimeout(() => {
        const result = mocaShippingAndPayment.find(q => q.quoteId === quoteId);
        resolve(result || ({} as IShippingAndPaymentForm));
      }, 2000)
    ),

  UpdateQuoteItem: (quoteItem: IQuoteItem) =>
    new Promise<string>(resolve => {
      setTimeout(() => {
        // update na base
        resolve('done!');
      }, 2000);
    }),

  GetQuoteItems: (quoteId: String) =>
    new Promise<IQuoteItem[]>(resolve =>
      setTimeout(() => {
        const result = mocaQuotesItems.filter(q => q.quoteId === quoteId);
        resolve(result?.length ? result : []);
      }, 2000)
    )
};
