import { IQuoteCSV, IQuoteItem } from '../Interfaces/QuotesDetail';

export const mapQuoteItemToQuoteCSV = (data: IQuoteItem[]) =>
  data.map(
    q =>
      ({
        Id: q.id,
        CodigoProduto: q.productCode,
        Disponivel: q.hasItem ? 'sim' : 'nao',
        CodigoDeReferencia: q.referenceCode,
        Produto: q.product,
        ValorUnitario: q.unitValue,
        ValorIPI: q.ipiValue,
        ValorICMS: q.icmsValue,
        PrazoEntrega: q.deliveryTime,
        Observacao: q.observation
      } as IQuoteCSV)
  );

export const mapQuoteCSVToQuoteItem = (data: IQuoteCSV[]) =>
  data.map(
    q =>
      ({
        id: q.Id,
        productCode: q.CodigoProduto,
        hasItem: q.Disponivel === 'sim',
        referenceCode: q.CodigoDeReferencia,
        product: q.Produto,
        unitValue: q.ValorUnitario,
        ipiValue: q.ValorIPI,
        icmsValue: q.ValorICMS,
        deliveryTime: q.PrazoEntrega,
        observation: q.Observacao
      } as IQuoteItem)
  );
