import React from 'react';

import { Container, Title, Total } from './styles';
import {
  IPaymentConditionsSelect,
  IQuoteItem,
  IShippingAndPaymentForm,
  IShippingTypeSelect
} from '../../Interfaces/QuotesDetail';
import { ISupplier } from '../../Interfaces/Supplier';
import { IBuyer } from '../../Interfaces/Buyer';

const TablePrint: React.FC<{
  info: string[][];
  title: string;
}> = ({ info, title }) => (
  <>
    <h2>{title}</h2>
    <table>
      <tbody>
        {info.map((row, index) => (
          <tr key={index.toString()}>
            {row.map((col, indexCol) => (
              <td key={indexCol.toString()}> {col}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  </>
);

interface Props extends React.HTMLAttributes<HTMLDivElement> {
  quote: IQuoteItem[];
  supplier: ISupplier;
  buyer: IBuyer;
  shipping: IShippingTypeSelect[];
  payment: IPaymentConditionsSelect[];
  paymentAndShipping: IShippingAndPaymentForm;
}

const headerSupplierBuyer = [
  'Razão Social',
  'Nome Fantasia',
  'CNPJ',
  'Endereço',
  'Bairro',
  'Cidade',
  'Estado',
  'CEP'
];

const headerQuote = [
  'Tipo Frete',
  'Valor Frete',
  'Data Emissão',
  'Data Limite',
  'Condição de Pagamento'
];

const headerQuoteItems = [
  'Item',
  'Código',
  'Descrição',
  'Código Ref.',
  'Qtd.',
  'Valor Unit.',
  'Total',
  'Aliq. IPI%',
  'Aliq. ICMS%',
  'Observação'
];

const QuoteDetailPrint: React.ForwardRefRenderFunction<
  HTMLDivElement,
  Props
> = (
  { quote, supplier, buyer, shipping, payment, paymentAndShipping },
  ref
) => {
  const infoSupplier: string[][] = [[...headerSupplierBuyer]];
  const infoBuyer: string[][] = [[...headerSupplierBuyer]];
  const infoQuote: string[][] = [[...headerQuote]];
  const infoQuoteItems: string[][] = [[...headerQuoteItems]];

  infoSupplier.push([
    ...[
      supplier.companyName,
      supplier.fantasyName,
      supplier.CNPJ,
      supplier.address,
      supplier.neighborhood,
      supplier.city,
      supplier.state,
      supplier.zipCode
    ]
  ]);

  infoBuyer.push([
    ...[
      buyer.companyName,
      buyer.fantasyName,
      buyer.cnpj,
      buyer.address,
      buyer.neighborhood,
      buyer.city,
      buyer.state,
      buyer.zipCode
    ]
  ]);

  infoQuote.push([
    ...[
      shipping.find(s => s.shippingTypeId === paymentAndShipping.shippingTypeId)
        ?.shippingType || '',
      paymentAndShipping?.shippingValue?.toString(),
      buyer.issuanceDate,
      buyer.deadlineDate,
      payment.find(
        p => p.paymentConditionsId === paymentAndShipping.paymentConditionsId
      )?.paymentCondition || ''
    ]
  ]);

  quote.forEach(q =>
    infoQuoteItems.push([
      ...[
        q.itemId,
        q.productCode,
        q.product,
        q.referenceCode,
        q.amount.toString(),
        q.unitValue,
        q.total,
        `${q.ipiValue}%`,
        `${q.icmsValue}%`,
        q.observation
      ]
    ])
  );

  const total = quote.reduce(
    (s, q, result) => (q?.total?.toFloat() || 0) + s,
    0
  );

  return (
    <Container ref={ref}>
      <Title>Cotação - {quote.length && quote[0].quoteId}</Title>
      <TablePrint title="Fornecedor" info={infoSupplier} />
      <TablePrint title="Comprador" info={infoBuyer} />
      <TablePrint title="Cotação" info={infoQuote} />
      <TablePrint title="Itens da Cotação" info={infoQuoteItems} />
      <Total>
        <span>Total</span> <span>R$ {total}</span>
      </Total>
    </Container>
  );
};

export default React.forwardRef(QuoteDetailPrint);
